h3, h4 {
  margin-bottom: 0;
}

.App > nav {
  border-bottom: 1px solid #023144;;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  display:block;
  z-index: 2;
  opacity: 0.9;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
  float: left;
}

nav a {
  text-decoration: none;
  color: #023144;
  font-weight: bold;
  padding: 20px 32px 17px 32px;
}

nav a:hover {
  /* background: black; */
  background: #023144;
  color: white;
}

#page-body {
  /* max-width: 700px; */
  margin: auto;
  padding-top: 64px;
  width: 68%;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.article_main_image_element, img.single_articlkeImage{
  max-width: 100%;
  height: auto;
}

.main_image_meta{
  color: #666;
  font-size: 1.4vw;
