/* Hamburger Styling Start */

.hamburger_icon{
  /* position: absolute; */
    top: 5%;
    left: 2rem;
    
    flex-direction: column;
    justify-content: space-around;
    width: 2rem;
    height: 2rem;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0px;
    display: none;
    z-index: 10;
    float: right;
    margin: 0 15px;
}

.hamburger_icon div{
    width: 2rem;
    height: 0.25rem;
    background: #023144;
    border-radius: 10px;
    transition: all 0.3s linear 0s;
    position: relative;
    transform-origin: 1px center;
}

.cross .bb_1{
  transform:  rotate(45deg);
}

.cross .bb_2{
    opacity: 0;
}

.cross .bb_3{
  transform:  rotate(-45deg);
}

div#BurgerMenu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #EFFFFA;
  /* transform: ${({ open;}) => open ? 'translateX(0)' : 'translateX(-100%)';} height: 100vh; */
  text-align: left;
  padding: 2rem;
  position: relative;
  top: 0;
  left: 0;
  transition: transform 0.3s ease-in-out;
  margin-top: 12px;
}

#BurgerMenu.invisMenu{
  /* transform: translateX(-100);
  transition: transform 0.3s ease-in-out; */
  display: none;
}

/* Hamburger Styling End */

